.Saved {
    background: white;
    height: 1000px;
    display: flex;
    background-size: cover;
  }

.Favs {
    height: 928px;
    width: 1790px;
    display: flex;
    background: url("data:image/jpeg;base64,");
    background-size: cover;
    padding: 24px;
    box-shadow: 0 8px 16px 0 rgb(0 0 0 / 24%), 0 0 8px 0 rgb(0 0 0 / 8%);
    border-radius: 16px;
    margin: auto;
    margin-top: 50px;
    flex-direction: column;
}

.searcheedd {
    text-align:center;
    font-size: 16px;
    color: #fff;
    height: 38px;
    width: 372px;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    box-sizing: border-box;
    background: transparent;
    padding: 0;
    border-radius: 14px;
    display: block;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-weight: 700;
    background-size: 24px;
    background-position: 16px;
    background-color: #69a041;
    margin: auto;
    margin-top: 0px;
    margin-bottom: 80px;
}
  